@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);

 

body {
  margin: 0;
  font-family: 'Anonymous Pros', monospace;
  color: #FFFFFF;
}

@font-face {
  font-family: Little Alien, 'Open Sans', sans-serif;
  src: url("/static/media/Little Alien.63b14c74.ttf");
}

body li {
  font-family: Little Alien, 'Open Sans', sans-serif;
}
code {
  font-family: 'Anonymous Pros', monospace;
}

/* Customize your main colors in :root variables */
:root {
  --main-background-color: #0f0f0e;
  --card-background-color: rgba(0,0,0,.4);
  --card-background-lighter-color: #7A7F92;
  --main-text-color: #ffffff;
  --title-text-color:rgb(65, 224, 16);
}

@font-face {
  font-family: Little Alien, 'Open Sans', sans-serif;
  src: url("/static/media/Little Alien.63b14c74.ttf");
}


body{
  background-color:#0f0f0e;
  background-color:var(--main-background-color);
  font-family: 'Anonymous Pros', monospace;
}



h2, p{
  color: #ffffff;
  color: var(--main-text-color);
}

h3{
font-weight: normal;
}

.MuiFab-extended{
  height: auto;
  padding-right: 0 !important;
}

.MuiFab-label{
  font-weight: bold;
}

.MuiPaper-root{
  background-color: rgba(0,0,0,.4) !important;
  background-color: var(--card-background-color) !important;
}

.MuiButton-containedPrimary:not(.MuiButton-fullWidth){
  border-radius: 24px !important;
  padding: 6px 16px;
}

.MuiButton-label{
  font-size: 1.2em;
}

.MuiLinearProgress-colorPrimary{
  background-color:#ffffff !important;
  background-color:var(--main-text-color) !important;
}

.MuiLinearProgress-barColorPrimary{
  background-color:rgb(65, 224, 16) !important;
  background-color:var(--title-text-color) !important;
}



